body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-input-placeholder {
  color: #8FA2BA;
  font-size: 16px;
  padding-left: 15px;
  font-family: Consolas, sans-serif;
  font-style: italic; }

::-ms-input-placeholder {
  color: #8FA2BA;
  font-size: 16px;
  padding-left: 15px;
  font-family: Consolas, sans-serif;
  font-style: italic; }

::placeholder {
  color: #8FA2BA;
  font-size: 16px;
  padding-left: 15px;
  font-family: Consolas, sans-serif;
  font-style: italic; }

.App {
  text-align: center; }

.container {
  max-width: 1920px;
  min-height: 100vh;
  background: #93cede;
  background: -webkit-linear-gradient(left, #93cede 0%, #43cba6 38%, #49a5bf 100%);
  background: linear-gradient(to right, #93cede 0%, #43cba6 38%, #49a5bf 100%); }

.main-section {
  display: -webkit-flex;
  display: flex;
  padding-top: 25px; }

.filter {
  min-width: 320px;
  padding: 0 15px;
  color: #000000;
  margin: 10px 0; }

.buttons-group {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.users {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center; }

.users .user {
  max-width: 200px;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 7px;
  box-shadow: 0px 10px 20px -1px rgba(0, 0, 0, 0.33);
  padding: 5px 25px;
  margin: 10px;
  color: #22354D;
  -webkit-transition: all .25s ease;
  transition: all .25s ease; }
  .users .user:hover {
    -webkit-transform: scale(0.99);
            transform: scale(0.99); }

.form-control {
  width: 100%;
  padding: 15px 0;
  border: none;
  border-radius: 7px;
  margin: 25px 0; }

.radio-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.sort-asc-desc {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.2); }

.form-check {
  padding: 8px; }

img {
  width: 100%;
  margin-top: 15px;
  border-radius: 50%;
  box-shadow: 0px 5px 10px -1px rgba(0, 0, 0, 0.33); }

.card.card__main {
  border: none;
  box-shadow: 0px 10px 20px -1px rgba(0, 0, 0, 0.33); }

.card {
  -webkit-transition: all .25s ease;
  transition: all .25s ease; }

.card__header {
  margin: 0; }

.list-item {
  margin: 5px 0; }

.card:hover {
  -webkit-transform: scale(0.99);
          transform: scale(0.99); }

.card__main .card-body {
  padding: .5rem;
  text-align: center; }

.card-body .list-group-item {
  padding: 5px 10px; }

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  z-index: 100; }

.pulse {
  position: relative;
  left: 50%;
  top: 50vh;
  margin-left: -40px;
  margin-top: -40px; }
  .pulse:before, .pulse:after {
    content: '';
    border: 5px solid #62DCC7;
    width: 80px;
    height: 80px;
    border-radius: 500px;
    position: absolute; }
  .pulse:before {
    -webkit-animation: pulse-outer .8s ease-in infinite;
            animation: pulse-outer .8s ease-in infinite; }
  .pulse:after {
    -webkit-animation: pulse-inner .8s linear infinite;
            animation: pulse-inner .8s linear infinite; }

@-webkit-keyframes pulse-outer {
  0% {
    opacity: 1;
    filter: alpha(opacity=100); }
  50% {
    opacity: 0.5;
    filter: alpha(opacity=50); }
  100% {
    opacity: 0;
    filter: alpha(opacity=0); } }

@keyframes pulse-outer {
  0% {
    opacity: 1;
    filter: alpha(opacity=100); }
  50% {
    opacity: 0.5;
    filter: alpha(opacity=50); }
  100% {
    opacity: 0;
    filter: alpha(opacity=0); } }

@-webkit-keyframes pulse-inner {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    filter: alpha(opacity=0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    filter: alpha(opacity=100); } }

@keyframes pulse-inner {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    filter: alpha(opacity=0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    filter: alpha(opacity=100); } }

.button {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #FD6347;
  padding: 15px 15px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  border-radius: 7px;
  text-align: center;
  position: relative;
  outline: none;
  -webkit-transition: background-color .1s ease;
  transition: background-color .1s ease; }
  .button::after {
    -webkit-transition: background-color .2s ease;
    transition: background-color .2s ease;
    position: absolute;
    content: '';
    height: 4px;
    bottom: 0;
    width: 100%;
    background-color: #440b01;
    opacity: .18;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    left: 0; }
  .button:focus, .button:hover {
    text-decoration: none;
    color: #fff; }
  .button:hover {
    background-color: #fd7860;
    cursor: pointer; }
    .button:hover::after {
      opacity: .22; }
  .button:active {
    background-color: #fd4e2e; }
    .button:active::after {
      opacity: .32; }
  .button:disabled {
    background-color: #919191;
    cursor: auto; }

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: block;
  color: #fff;
  font-weight: bold; }

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #ddd;
  border-radius: 100%;
  background: #fff; }

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  width: 12px;
  height: 12px;
  background: tomato;
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0); }

[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1); }

/* media rules*/
@media screen and (max-width: 980px) {
  .main-section {
    -webkit-flex-direction: column;
            flex-direction: column; } }

@media screen and (max-width: 500px) {
  .users .user {
    min-width: 75%; } }

@media screen and (max-width: 400px) {
  .filter {
    min-width: 290px; } }

.sort-asc-desc {
  /* box-shadow: 0px 2px 5px 1px tomato; */
  border-radius: 7px;
  margin-top: 25px;
}

